.project > img{
    width: 350px;
    height: 170px;
}

.project{
    margin: 15px;
    width: 350px;
    height: 170px;
    transition: scale 100ms ease-in;

}
.project:hover{
    color: antiquewhite;
    background-color: brown;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: scale(1.05);
    cursor: pointer;


}
.project__content{
    padding: 10px;
    
}
.project__content > h4{
    padding: 5px;
}
.project__content> p{
    font-size: 15px;

}