
  @import url('https://fonts.googleapis.com/css2?family=Mansalva&display=swap');


.skillcontainer{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 200px;

}
.skillcontainer__txt{
    color: antiquewhite;
    flex: 0.60;
    text-align: left;
    max-width: 500px;
    margin: 20px;


}
.skillcontainer__txt>h2{
    font-size: 30px;

}
.skillcontainer__img{
    flex: 0.4;
    margin-right: 100px;
    text-align: center;

}
.skillcontainer__img>img{
    width: 400px;
    border-radius: 3px;
    object-fit: contain;
}
.skillcontainer__skillset>h5{
    font-family: 'Mansalva', cursive;
}
.skillcontainer__slider1>div{
    background-color: blue;
}
.skillcontainer__slider2>div{
    background-color: rgb(24, 184, 90);
}