 @import url('https://fonts.googleapis.com/css2?family=Belanosima&display=swap');

 @import url('https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap');
.header {
    display: flex;
    align-items: center;
    color: aliceblue;
    background-color: black;
    position: sticky;
    z-index: 2000;
    top: 0;

}
.header__left{
    flex: 0.4;

}
.header__left>h1{
    font-family: "Belanosima", sans-serif;
    align-items: center;
    display: flex;
    padding: 20,0;
    
}
.header__left>h1>span{
    color: brown;
}
.header__right{
    flex: 0.6;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

}
.header__right>a>h4{
    transition: transform 200ms ease-in-out;
    cursor: pointer;
    font-size: 10px;
    font-family: sans-serif;
    letter-spacing: 1px;
    padding: 20px,0;

}

.header__right>a>h4:hover{
    transform: scale(1.09);
    border-radius: 1px;
    color: antiquewhite;
    background-color: brown;
    border-radius: 2px;
    letter-spacing: 2px;
    font-family: sans-serif;
    font-size: 18px;
    cursor: pointer;

}
.header__rightbutton{
    color: antiquewhite;
    background-color: brown;
    border-radius: 2px;
    padding: 20px,0;
    cursor: pointer;

}