@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;600&display=swap');

.topcontent{
    color: antiquewhite;
    position: relative;
}
.topcontent__container{
    position: absolute;
    top: 300px;
    left: 100px;

}
.topcontent__container>p{
    letter-spacing: 1px;
    padding-top: 20px;
    padding-right: 3px;
    text-align: center;
}

.topcontent__container>a> button{
    margin-top: 30px;
    margin-left: 3px;
    text-align: center;
    border: none;
    border-radius: 2px;
outline: none;
}
.topcontent__container>h1{
    font-size: 40px;
    font-family: "Roboto",sans-serif;
}
.topcontent__downloadbutton{
    color: antiquewhite;
    background-color: brown;
    font-size: 14px;
    font-family: "Roboto",sans-serif;
    margin-right: 20px;
    padding: 10px,30px;
    font-weight: 300;

}
.topcontent__downloadbutton:hover {
    color: black;
    background-color: antiquewhite;
    font-weight: 600px;
    font-family: "Roboto",sans-serif;
}

.topcontent__workbutton{
    color: antiquewhite;
    background-color: transparent;
    padding: 9px,45px;
    border: 1px solid white !important ;
    font-family: "Roboto",sans-serif;
    font-weight: 300px;
    cursor: pointer;


}
.topcontent__workbutton:hover{
    color: blue;
    background-color: black;
    font-weight: 600px;
    font-family: "Roboto",sans-serif;
}