.contactt{
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
    margin: 200px,0,100px;
    align-items: center;
}

.contact__container{
    display: flex;
    border:  1px solid white;
    padding: 20px;
    margin-top: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 150px;
    width: 300px;


}
.contact__container>p{
    font-size: 20px;
    text-align: left;
    padding-bottom: 30px;

}
.contact__container>span{
    color: blueviolet;
}